nt-weight: 900;\n  font-style: normal;\n  font-display: swap;\n}\n\n/*\n  Josh's Custom CSS Reset\n  https://www.joshwcomeau.com/css/custom-css-reset/\n*/\n\n*,\n*::before,\n*::after {\n  box-sizing: border-box;\n}\n\n* {\n  margin: 0;\n}\n\n@media (prefers-reduced-motion: no-preference) {\n  html {\n    interpolate-size: allow-keywords;\n  }\n}\n\nbody {\n  line-height: 1.5;\n  -webkit-font-smoothing: antialiased;\n}\n\nimg,\npicture,\nvideo,\ncanvas,\nsvg {\n  display: block;\n  max-width: 100%;\n}\n\ninput,\nbutton,\ntextarea,\nselect {\n  font: inherit;\n}\n\np,\nh1,\nh2,\nh3,\nh4,\nh5,\nh6 {\n  overflow-wrap: break-word;\n}\n\np {\n  text-wrap: pretty;\n}\n\nh1,\nh2,\nh3,\nh4,\nh5,\nh6 {\n  text-wrap: balance;\n}\n\n#root,\n#__next {\n  isolation: isolate;\n}\n\n/* VARIABLES */\n\n:root {\n  --system-ui:\n    system-ui, -apple-system, BlinkMacSystemFont, \"Segoe UI\", Roboto, Oxygen,\n    Ubuntu, Cantarell, \"Open Sans\", \"Helvetica Neue\", sans-serif;\n  --board-size: 500px;\n  --border-radius: 30px;\n  --cell-size: calc(var(--board-size) / 10);\n  --background-color: #192a51;\n  --board-color: #233d4d;\n  --hit-color: #ff595e;\n  --miss-color: #fbfbf2;\n  --ship-color: #19191a;\n  --active-color: #3498db;\n  --box-shadow: 0 0 10px rgba(52, 152, 219, 0.6);\n  --hover-color: #54b2f1;\n}\n\n/* BASIC LAYOUT */\n\nbody {\n  visibility: hidden;\n  font-family: \"Kimberley\", var(--system-ui);\n  background-color: var(--background-color);\n}\n\nbody.ready {\n  visibility: visible;\n}\n\n#page {\n  display: grid;\n  gap: 1em;\n  grid-template-rows: 100px 1fr 50px;\n  margin: 0 auto;\n}\n\nheader {\n  justify-items: center;\n}\n\nheader h1 {\n  font-family: \"Ethnocentric\", var(--system-ui);\n  padding: 8px;\n  font-size: 3rem;\n  color: var(--active-color);\n  text-shadow: 8px 8px var(--ship-color);\n}\n\nmain {\n  display: grid;\n  grid-template-rows: 1fr, 100px;\n  justify-items: center;\n}\n\n#content {\n  display: flex;\n  gap: 4em;\n}\n\n#button-menu {\n  margin-top: 3em;\n}\n\nbutton {\n  cursor: pointer;\n  border-radius: var(--border-radius);\n  color: var(--active-color);\n  font-weight: 900;\n  background-color: var(--background-color);\n  border-color: var(--active-color);\n  box-shadow: var(--box-shadow);\n  transition: transform 0.3s ease-in-out;\n}\n\nbutton:hover {\n  color: var(--hover-color);\n  box-shadow: 0 0 10px rgba(75, 157, 211, 0.6);\n  transform: scale(1.2);\n}\n\n/* MENU */\n\n.menu {\n  margin-top: 3em;\n  display: flex;\n  flex-direction: column;\n  gap: 2em;\n}\n\n.menu img {\n  width: 400px;\n  height: auto;\n  margin-bottom: 3em;\n}\n\n.single-player,\n.pvp {\n  padding: 0.5em 1em;\n  font-size: 2rem;\n}\n\n/* PVP NAME */\n\n.pvp-setup {\n  margin-top: 2em;\n  color: var(--miss-color);\n  width: var(--board-size);\n}\n\n.pvp-setup > h1 {\n  font-size: 2.5rem;\n  margin-bottom: 1.5em;\n}\n\n.pvp-setup > p {\n  margin-bottom: 2em;\n}\n\n.pvp-setup > button {\n  font-size: 1.25rem;\n  padding: 6px 12px;\n  margin-bottom: 4em;\n}\n\n.pvp-name {\n  display: flex;\n  flex-direction: column;\n  gap: 8px;\n  margin-bottom: 1.5em;\n}\n\n.pvp-name label {\n  font-size: 1.5rem;\n}\n\n.pvp-name input {\n  padding-left: 10px;\n  width: 14.5em;\n  background-color: var(--background-color);\n  color: var(--miss-color);\n  border-radius: 10px;\n  border: 4px solid var(--active-color);\n  outline: none;\n  font-size: 2rem;\n}\n\n.pvp-name input:focus {\n  border-color: var(--hover-color);\n}\n\n.pvp-setup > p {\n  font-size: 1.25rem;\n  font-weight: 900;\n}\n\n/* BOARD SETUP */\n\n.info {\n  color: var(--miss-color);\n  align-content: center;\n  width: var(--board-size);\n}\n\n.info h2 {\n  font-size: 2rem;\n  padding-bottom: 1em;\n}\n\nul {\n  display: flex;\n  flex-direction: column;\n  gap: 1em;\n  padding: 0;\n  font-size: 1.25rem;\n}\n\n.setup > .cell {\n  cursor: default;\n}\n\n.setup > .ship {\n  background-color: var(--ship-color);\n  cursor: grab;\n}\n\n.player1 > .ship.dragging,\n.player2 > .ship.dragging {\n  background-color: #e2e8ddb9;\n}\n\n.ghost,\n.cell.ship.ghost {\n  background-color: #b7d1da7c;\n}\n\n/* BUTTON MENU */\n\n.options {\n  display: flex;\n  gap: 1.5em;\n}\n\n.options > button,\n.back-btn,\n.exit-btn,\n.again-btn {\n  font-size: 1.25rem;\n  padding: 6px 12px;\n}\n\n/* GAMEBOARD */\n\n.player1-tag.active h1,\n.player2-tag.active h1 {\n  border: 2px solid var(--active-color);\n  border-radius: var(--border-radius);\n}\n\n.board-grid.player1.active,\n.board-grid.player2.active {\n  border: 2px solid var(--active-color);\n}\n\n.board-container {\n  align-content: center;\n  color: var(--miss-color);\n  display: grid;\n  grid-template-columns: var(--cell-size) var(--board-size);\n  grid-template-rows: 80px var(--cell-size) var(--board-size);\n}\n\n.player1-tag,\n.player2-tag {\n  display: grid;\n  justify-content: center;\n  grid-row: 1/2;\n  grid-column: 1/3;\n}\n\n.player1-tag h1,\n.player2-tag h1 {\n  font-size: 1.5rem;\n  padding: 8px 16px;\n  align-content: center;\n}\n\n.x-coords {\n  display: grid;\n  grid-template-columns: repeat(10, 1fr);\n  grid-row: 2/3;\n  grid-column: 2/3;\n}\n\n.x-cell,\n.y-cell {\n  align-content: center;\n  text-align: center;\n}\n\n.y-coords {\n  display: grid;\n  grid-template-rows: repeat(10, 1fr);\n  grid-row: 3/4;\n  grid-column: 1/2;\n}\n\n.board-grid {\n  grid-column: 2/3;\n  grid-row: 3/4;\n  display: grid;\n  grid-template-rows: repeat(10, 1fr);\n  grid-template-columns: repeat(10, 1fr);\n  border: 2px solid var(--board-color);\n  width: var(--board-size);\n  height: var(--board-size);\n}\n\n.cell {\n  cursor: crosshair;\n  border: 2px solid var(--board-color);\n}\n\n.show-ships > .ship {\n  background-color: var(--ship-color);\n}\n\n.player1 > .cell.ship.hit,\n.player2 > .cell.ship.hit {\n  background-color: var(--hit-color);\n}\n\n.miss {\n  background-color: var(--miss-color);\n}\n\n/* MODAL */\n\ndialog:not([open]) {\n  display: none;\n}\n\ndialog {\n  position: fixed;\n  margin: 0 auto;\n  transform-origin: top;\n  font-size: 2rem;\n  background-color: var(--background-color);\n  border: 2px solid var(--active-color);\n  box-shadow: var(--box-shadow);\n  border-radius: var(--border-radius);\n  padding: 1em;\n}\n\n.winner-text {\n  color: var(--miss-color);\n  margin-bottom: 1em;\n}\n\n.modal-btns {\n  display: grid;\n  grid-template-columns: repeat(2, 1fr);\n  gap: 1em;\n}\n\n/* ANIMATIONS */\n\n/* BOARD ACTIONS */\n.cell.miss,\n.cell.hit,\n.setup > .ship,\n.cell.ship.hit {\n  animation: pop-in 150ms ease-out forwards;\n  will-change: transform;\n}\n\n@keyframes pop-in {\n  0% {\n    transform: scale(0.2);\n    opacity: 0;\n  }\n  80% {\n    transform: scale(1.1);\n    opacity: 1;\n  }\n  100% {\n    transform: scale(1);\n  }\n}\n\nh1,\n.board-grid {\n  transition:\n    border-color 0.4s ease,\n    box-shadow 0.4s ease,\n    border-radius 0.4 ease;\n}\n\n/* SWITCHING ACTIVE PLAYER */\n\n.board-grid.active,\n.active > h1 {\n  border-color: var(--active-color) !important;\n  box-shadow: var(--box-shadow);\n}\n\n/* LOADING DOM */\n\n.fade-in {\n  opacity: 0;\n  animation: fadeIn 500ms ease forwards;\n}\n\n@keyframes fadeIn {\n  to {\n    opacity: 1;\n  }\n}\n\n.slide-in {\n  animation: slideIn 1s ease-in-out both\n}\n\n@keyframes slideIn {\n  0% {\n    transform: translateY(0);\n    opacity: 0;\n  }\n\n  25% {\n    opacity: 0.3;\n  }\n\n  50% {\n    opacity: 0.8;\n  }\n\n  75% {\n    transform: translateY(13em);\n    opacity: 1;\n  }\n\n  100% {\n    transform: translateY(12em);\n    opacity: 1;\n  }\n}\n"],"sourceRoot":""}]);
